import { useEffect, useState } from "react";
import PropTypes from "prop-types";

import Form, { useForm } from "@miq/form";
import { AdminView } from "@miq/adminjs";

import { employeeServices } from "./utils";

const propTypes = {};

const EmployeeCreateForm = (props) => {
    const form = useForm({ first_name: "", last_name: "", private_email: "" });
    const saveEmployee = (e) => {
        e.preventDefault();
    };

    return (
        <Form context={form} onSubmit={saveEmployee}>
            <div className="d-grid grid-md-12 mb-1">
                <Form.Label value="Firstname" className="span-12 mb-0" />
                <Form.TextInput
                    required
                    name="first_name"
                    error={form.errors.first_name}
                    className="span-md-6"
                    minLength={2}
                />
            </div>

            <div className="d-grid grid-md-12 mb-2">
                <Form.Label value="Lastname" className="span-12 mb-0" />
                <Form.TextInput
                    required
                    name="last_name"
                    error={form.errors.last_name}
                    className="span-md-6"
                    minLength={2}
                />
            </div>
            <div className="d-grid grid-md-12 mb-2">
                <Form.Label value="Email" className="span-12 mb-0" />
                <Form.TextInput
                    required
                    type="email"
                    name="private_email"
                    error={form.errors.private_email}
                    className="span-md-6"
                />
            </div>

            <div className="">
                <Form.Submit value="Save employee" className="miq-btn" />
            </div>
        </Form>
    );
};

export default function EmployeeCreateView(props) {
    const { compSlug } = props.match.params;

    return (
        <AdminView className="employee-create-view miq-container" title="Add an employee">
            <EmployeeCreateForm {...props} />
        </AdminView>
    );
}

EmployeeCreateView.propTypes = propTypes;
